/**
* Loading Indicator
* ============================================================================
*/
.loading {
  margin: 6em auto;
  position: relative;
}
.loading.overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(255, 255, 255, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
}
.loading.overlay.dark {
  background-color: rgba(0, 0, 0, 0.6);
}
.loading .loading-indicator {
  position: relative;
  border: 6px solid #e6e9ec;
  border-left-color: #6c5fc7;
  -webkit-animation: loading 0.5s infinite linear;
  animation: loading 0.55s infinite linear;
  margin: 0 auto;
}
.loading .loading-indicator .checkmark.draw:after {
  animation-duration: 800ms;
  animation-timing-function: ease;
  animation-name: checkmark;
  transform: scaleX(-1) rotate(135deg);
}
.loading .loading-indicator .checkmark:after {
  opacity: 1;
  height: 32px;
  width: 16px;
  transform-origin: left top;
  border-right: 6px solid #57be8c;
  border-top: 6px solid #57be8c;
  content: '';
  left: 10px;
  top: 30px;
  position: absolute;
}
.loading .loading-indicator,
.loading .loading-indicator:after {
  border-radius: 50%;
  width: 64px;
  height: 64px;
}
.loading .loading-message {
  margin-top: 20px;
  text-align: center;
}
.loading .load-complete {
  -webkit-animation: none;
  animation: none;
  border-color: #57be8c;
  transition: border 500ms ease-out;
}
@keyframes checkmark {
  0% {
    height: 0;
    width: 0;
    opacity: 1;
  }
  20% {
    height: 0;
    width: 16px;
    opacity: 1;
  }
  40% {
    height: 32px;
    width: 16px;
    opacity: 1;
  }
  100% {
    height: 32px;
    width: 16px;
    opacity: 1;
  }
}
@-webkit-keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes loading {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
.loading.mini {
  margin: 4px 0;
  font-size: 13px;
}
