/**
 * HoverCard
 * ============================================================================
 */
.hovercard-container {
  position: relative;
}
.hovercard {
  border-radius: 4px;
  background-clip: padding-box;
  background: #fff;
  color: #2f2936;
  text-align: left;
  position: absolute;
  display: block;
  padding: 0;
  line-height: 1;
  z-index: 1000;
  box-shadow: 0 0 35px 0 rgba(67, 62, 75, 0.2);
  border: 1px solid #e2dee6;
  white-space: initial;
  font-family: 'Lato', 'Avenir Next', 'Helvetica Neue', sans-serif;
  bottom: 28px;
  left: 50%;
  margin-left: -147.5px;
  width: 295px;
  visibility: hidden;
  animation: hovercardFadeIn 100ms ease-in-out, hovercardSlideDown 100ms ease-in-out;
  animation-play-state: paused;
  /** ISSUE LINK HAX **/
  /** /ISSUE GROUP HAX **/
}
@keyframes hovercardSlideUp {
  from {
    transform: translateY(12px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes hovercardSlideDown {
  from {
    transform: translateY(-12px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes hovercardFadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.hovercard.visible {
  visibility: visible;
  animation-play-state: running;
}
.hovercard.hovercard-bottom {
  bottom: initial;
  top: 28px;
  animation: hovercardFadeIn 100ms ease-in-out, hovercardSlideUp 100ms ease-in-out;
}
.hovercard.hovercard-bottom .hovercard-hoverlap {
  top: -16px;
}
.hovercard.hovercard-bottom:before,
.hovercard.hovercard-bottom:after {
  bottom: initial;
  top: -20px;
  transform: rotate(180deg);
}
.hovercard.hovercard-bottom:before {
  top: -21px;
}
.hovercard.hovercard-bottom.with-header:after {
  border-top-color: #fbfbfc;
}
.hovercard.hovercard-left {
  left: 0;
  margin-left: 0;
}
.hovercard.hovercard-left:before,
.hovercard.hovercard-left:after {
  left: 21px;
  margin-left: 0;
}
.hovercard.hovercard-right {
  left: initial;
  right: 0;
  margin-left: 0;
}
.hovercard.hovercard-right:before,
.hovercard.hovercard-right:after {
  left: initial;
  right: 21px;
  margin-left: 0;
}
.hovercard:before,
.hovercard:after {
  width: 0;
  height: 0;
  content: '';
  display: block;
  position: absolute;
  z-index: -1;
  bottom: -20px;
  left: 50%;
  margin-left: -10px;
  border: 10px solid transparent;
  border-top-color: #fff;
}
.hovercard:before {
