/* Language & Framework frame icons */
.frame {
  position: relative;
}
.frame.cocoa:before {
  content: '';
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
  right: -1px;
  height: 1px;
  background: #3b6ecc;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.6);
}
.frame.cocoa:after {
  content: '\e912';
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  color: #fff;
  font-size: 11px;
  background: #3b6ecc;
  text-align: center;
  line-height: 20px;
  top: -1px;
  left: -20px;
  border-radius: 3px 0 0 3px;
}
.frame.cocoa:after {
  font-family: 'sentry-simple';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.frame.cocoa + .cocoa:before,
.frame.cocoa + .cocoa:after {
  display: none;
}
.frame.objc:before {
  content: '';
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
  right: -1px;
  height: 1px;
  background: #3b6ecc;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.6);
}
.frame.objc:after {
  content: '\e912';
  display: block;
  position: absolute;
  width: 20px;
  height: 20px;
  color: #fff;
  font-size: 11px;
  background: #3b6ecc;
  text-align: center;
  line-height: 20px;
  top: -1px;
  left: -20px;
  border-radius: 3px 0 0 3px;
}
.frame.objc:after {
  font-family: 'sentry-simple';
  speak: none;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.frame.objc + .objc:before,
.frame.objc + .objc:after {
  display: none;
}
.frame.javascript:before {
  content: '';
  display: block;
  position: absolute;
  top: -1px;
  left: 0;
  right: -1px;
  height: 1px;
  background: #ecd744;
  box-shadow: 0 1px 0 rgba(255, 255, 255, 0.6);
}
