}
.onboarding-wrapper ul li.skipped h4 {
  color: #968ba0;
  text-decoration: line-through;
}
.onboarding-wrapper ul li.pending {
  position: relative;
}
.onboarding-wrapper ul li.pending .pending-bar {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  background-size: 30px 30px;
  background-image: linear-gradient(135deg, rgba(0, 0, 0, 0.03) 25%, transparent 25%, transparent 50%, rgba(0, 0, 0, 0.03) 50%, rgba(0, 0, 0, 0.03) 75%, transparent 75%, transparent);
  animation: animate-stripes 3s linear infinite;
}
.onboarding-wrapper ul li.blur .todo-content {
  -webkit-filter: blur(3px);
}
.onboarding-wrapper ul li:hover .skip-btn {
  opacity: 0.85;
  visibility: visible;
}
.onboarding-wrapper ul li:hover .skip-btn:hover {
  opacity: 1;
}
.onboarding-wrapper .ob-checkbox {
  width: 44px;
  height: 44px;
  background: #fff;
  border: 3px solid #e2dee6;
  border-radius: 46px;
  position: absolute;
  top: -5px;
  left: -58px;
}
.onboarding-wrapper .ob-checkbox .icon-checkmark {
  position: relative;
  color: #57be8c;
  font-size: 20px;
  left: 10px;
  top: 9px;
}
.onboarding-wrapper .ob-checkbox .icon-x {
  position: relative;
  color: #9586a5;
  font-size: 20px;
  left: 9px;
  top: 9px;
}
.onboarding-wrapper .ob-checkbox .icon-ellipsis {
  position: relative;
  font-size: 24px;
  color: #9586a5;
  top: 8px;
  left: 7px;
}
.onboarding-wrapper .skip-btn {
  position: absolute;
  z-index: 100;
  right: 5px;
  top: 2px;
  -webkit-transition: opacity 0.1s;
  -o-transition: opacity 0.1s;
  transition: opacity 0.1s;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  color: #625471;
}
.onboarding-wrapper .skip-btn:active {
  position: absolute;
}
.onboarding-wrapper .ob-confirmation {
  position: absolute;
  z-index: 100;
  background: rgba(255, 255, 255, 0.75);
  left: -15px;
  right: -15px;
  bottom: -10px;
  top: -20px;
  padding: 15px;
  text-align: center;
  font-size: 18px;
}
